// style.scss
.wp-block-create-block-carlos-gutenberg-blocks {
	.cgb-floating-boxes {
			position: relative;
			width: 100%;
			margin: 0 auto;
	}

	.cgb-group {
			position: relative;
			width: var(--box-size, 30vw);
			aspect-ratio: var(--aspect-ratio, 1/1);
			margin: 0 auto;

			@media (max-width: 768px) {
					width: var(--mobile-box-size, 80vw);
			}
	}

	.cgb-floating-box {
			position: absolute;
			width: 100%;
			height: 100%;
			border-radius: 20px;
			overflow: hidden;
			display: flex;
			justify-content: center;
			align-items: center;
			transition: all 0.3s ease;
			box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
			z-index: 3;

			img {
					width: 100%;
					height: 100%;
					object-fit: cover;
			}

			// Alineación derecha
			&.align-right {
					left: 0;
					right: unset;
			}

			&.cgb-second.align-right {
					transform: translateY(-10px) translateX(10px);
					z-index: 2;
			}

			&.cgb-third.align-right {
					transform: translateY(-20px) translateX(20px);
					z-index: 1;
			}

			// Alineación izquierda
			&.align-left {
					left: unset;
					right: 0;
			}

			&.cgb-second.align-left {
					transform: translateY(-10px) translateX(-10px);
					z-index: 2;
			}

			&.cgb-third.align-left {
					transform: translateY(-20px) translateX(-20px);
					z-index: 1;
			}

			// Efecto hover para el frontend
			@media (hover: hover) {
					&:hover {
							transform: translateY(-5px);
							box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
					}

					&.cgb-second:hover {
							transform: translateY(-15px) translateX(10px);
							
							&.align-left {
									transform: translateY(-15px) translateX(-10px);
							}
					}

					&.cgb-third:hover {
							transform: translateY(-25px) translateX(20px);
							
							&.align-left {
									transform: translateY(-25px) translateX(-20px);
							}
					}
			}
	}
}